main {
    position: absolute;
    right: 0;
    left: 30rem;
}

subnav {
    height: 100%;
    background-color: #f2f2f2;
    position: fixed;
    width: 10%;
    width: 24rem;
    top: 0;
    left: 6rem;
    right: 0;
}

.content {
    //margin: 7rem 12rem;
    height: 50rem;
    background: white;

    .header {
        position: relative;
        background: #262626;

        .bg {
            background-color: #262626;
            background-image: url('https://images.unsplash.com/photo-1484268234627-2278797bec04?dpr=2&auto=format&fit=crop&w=1500&h=1000&q=80&cs=tinysrgb&crop=');
            background-position: center center;
            background-attachment: fixed;
            background-size: cover;
            text-align: center;
            height: 500px;
            width: 100%;
            transition: background-image 100ms ease-in;

            animation-name: fadeIn;
            animation-iteration-count: 1;
            animation-timing-function: ease-in-out;
            animation-duration: 1s;
            animation-fill-mode: forwards;
            @keyframes fadeIn {
                0% {
                    opacity: 0;
                }

                100% {
                    opacity: 1;
                }
            }

            :fullscreen a {
    display: flex
}
        }

        .name {
            position: absolute;
            background: rgba(0, 0, 0, 0.42);
            width: 100%;
            height: 100%;
            top: 0;
        }
    }

    .description {
        width: 50rem;
        margin: 0 auto;
        height: 300rem;
    }
}

img {
    width: 100%;
    height: 100%;
}
